.carousel-container {
  width: 100%;
  max-width: 480px;
  margin: 0 auto 1.5rem auto;
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

.carousel-wrapper {
  position: relative;
  width: 100%;
  height: 220px;
  overflow: hidden;
  border-radius: 15px;
}

.carousel-slides {
  display: flex;
  width: 400%; /* 4张图片，每张100% */
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.carousel-slide {
  width: 25%; /* 100% / 4 */
  height: 100%;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.carousel-image {
  width: 110%;
  height: 110%;
  object-fit: cover;
  display: block;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  transform: scale(0.95);
  border-radius: 10px;
}

/* 导航箭头 */
.carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.4);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
  backdrop-filter: blur(10px);
}

.carousel-arrow:hover {
  background: rgba(0, 0, 0, 0.7);
  transform: translateY(-50%) scale(1.1);
}

.carousel-arrow-left {
  left: 15px;
}

.carousel-arrow-right {
  right: 15px;
}

/* 指示器 */
.carousel-indicators {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 10;
}

.carousel-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.carousel-indicator.active {
  background: white;
  border-color: white;
  transform: scale(1.2);
}

.carousel-indicator:hover {
  border-color: rgba(255, 255, 255, 0.8);
  transform: scale(1.1);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .carousel-container {
    max-width: 95%;
    margin: 0 auto 1rem auto;
  }
  
  .carousel-wrapper {
    height: 180px;
  }
  
  .carousel-arrow {
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
  
  .carousel-arrow-left {
    left: 10px;
  }
  
  .carousel-arrow-right {
    right: 10px;
  }
  
  .carousel-indicators {
    bottom: 12px;
    gap: 8px;
  }
  
  .carousel-indicator {
    width: 8px;
    height: 8px;
  }
}

@media (max-width: 480px) {
  .carousel-wrapper {
    height: 140px;
  }
  
  .carousel-arrow {
    width: 30px;
    height: 30px;
    font-size: 16px;
  }
  
  .carousel-arrow-left {
    left: 8px;
  }
  
  .carousel-arrow-right {
    right: 8px;
  }
  
  .carousel-indicators {
    bottom: 10px;
    gap: 6px;
  }
  
  .carousel-indicator {
    width: 6px;
    height: 6px;
  }
} 